.top_header {
    background: transparent url(../images/header_line.png) repeat-x right top;
}

.game_header {
    background: transparent url(../images/header.png) no-repeat center top;
    height: 100px;
}

.t-navbar {
    background: rgb(239, 239, 239);
    background-image: linear-gradient(rgb(255, 255, 255), rgb(215 215 215));
    max-height: 45px;
    border-top: 1px solid silver;
    box-shadow: 0px 4px 4px 0px #b9b9b9;
    /* background-position: 0 -28px; */
    /* background: url(../images/mw.png) repeat-x; */
}

.t-navbar .nav-link button {
    padding: 3px 5px;
}

.t-navbar .nav-link {
    margin: 0px 5px;
}

.t-navbar img {
    user-select: none;
}

.msTime {
    float: right;
    font-size: 14px;
    position: absolute;
    right: 0px;
}