body {
  direction: rtl !important;
  text-align: right;
  position: relative;
}

.middle-row {
  margin-top: 20px !important;
  min-height: 550px;
  padding-bottom: 5vh !important;
}

.navi-col {
  background: transparent url(./images/shadow_a.png) no-repeat left top;
}

.side-col {
  background: transparent url(./images/shadow_b.png) no-repeat right top;
}

/* .middle-col {
  border-right: 1px dashed gray;
  border-left: 1px dashed gray;
} */

.footer-row {
  background-color: #e7e7e7;
  width: 100%;
  padding: 5px;
  text-align: center;
  border-top: 1px solid silver;
}

.side_navi, .side_info, .content {
  margin-top: 20px;
}

#root>div {
  width: 100%;
  padding: 0;
}

#root>div>.row {
  margin: 0;
}

#root>div>.row>.col {
  padding: 0;
}